html, body {
	height: 100%;
}

body {
	font-family: Helvetica;
	margin: 0;
	padding: 0;
	background: linear-gradient(180deg, #7662bb 0%, #371d88 100%);
	color: #fff;
}

button {
	background-color: #1646a0;
	color: #fff;
	padding: 0 45px;
	border: 2px solid #fff;
	border-radius: 25px;
	height: 50px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 1.2em;
	cursor: pointer;
	transition: 0.4s;
}
  
button:hover {
	background: #0923af;
}